export default {
  data() {
    return {
      title: "记录",
      searchinput: "",
      currentPage: 1,
      limit: 10,
      total: 0,
      visible: false,
      fileList: [],
      tableData: [],
      form: {
        id: -1,
        title: "",
        type_id: "",
        description: "",
        image_url: "",
        tag: "",
        start_time: "",
        end_time: "",
        sort: "",
        show_status: ""
      },
      rules: {
        title: [
          { required: true, message: "必填项不可以为空", trigger: "blur" }
        ],
        type_id: [
          { required: true, message: "必填项不可以为空", trigger: "blur" }
        ],
        description: [
          { required: true, message: "必填项不可以为空", trigger: "blur" }
        ],
        start_time: [
          { required: true, message: "必填项不能为空", trigger: "blur" }
        ],
        end_time: [
          { required: true, message: "必填项不能为空", trigger: "blur" }
        ],
        sort: [
          {
            required: true,
            message: "必填项不能为空,只能输入数字",
            trigger: "blur"
          }
        ],
        show_status: [
          { required: true, message: "必填项不能为空", trigger: "blur" }
        ]
      }
    };
  },
  created() {
    this.initdata({ page: this.currentPage, limit: this.limit, title: this.searchinput });
  },

  methods: {

    //点击已经上传的图片
    handlePreview(file) {
      console.log(file);
      
    },

    //移除上传文件时
    handleRemove() {},

    //添加
    add(formName) {
      this.editForm('记录', true,{})
      // this.visiblity = true;
      // this.title = "记录";
       this.$refs[formName].resetFields();
    },

    //搜索
    search() {
      if (this.searchinput === "") {
        this.$message({
          type: "warning",
          message: "请输入你要搜索的标题!"
        });
      }
      this.currentPage = 1;
      this.limit = 10;
      this.initdata({ page: this.currentPage, limit: this.limit, title: this.searchinput });
    },

    //页容量发生变化
    handleSizeChange(num) {
      // console.log(num);
      this.limit = num;
      this.currentPage = 1;
      this.initdata({ page: this.currentPage, limit: this.limit, title: this.searchinput });
    },

    //页码发生变化
    handleCurrentChange(pagenum) {
      // console.log(pagenum);
      this.currentPage = pagenum;
      this.initdata({ page: this.currentPage, limit: this.limit, title: this.searchinput });
    },

    //表单关闭
    dialogFormVisible() {
      // this.visiblity = false;
       this.editForm("添加", false, {});
    },

    //表格编辑
    handleEdit(row,formName) {
      // console.log(row);
      this.form = { ...row };
      function data(time) {
        let long1 = Date.parse(time);
        let long2 = new Date(long1).getTime();
        return long2;
      }
      // this.form = this.formateNum(row)
      this.form.start_time = data(row.start_time);
      this.form.end_time = data(row.end_time);
      // console.log(this.form);
      // this.visiblity = true;
      // this.title = "编辑";
      this.editForm('编辑', true, this.form)
      this.$refs[formName].resetFields();
    },

    //表格删除
    handleDelete(y, row) {
      console.log(y, row.id);
      this.$confirm("确认删除吗？", "信息", {
        confirmButtonText: "确定",
        cancelButtonText: "取消",
        type: "warning"
      })
        .then(async () => {
          //2.发送请求在后台删除数据
          let { data } = await this.$http.HallFunConfig.DeleteGameNotice({
            id: row.id
          });
          console.log(data);
          if(data.code === 1 && data.msg === 'ok'){
               this.initdata({ page: this.currentPage, limit: this.limit, title: this.searchinput });
            }
          this.$message({
            type: "success",
            message: "删除成功!"
          });
        })
        .catch(() => {
          this.$message({
            type: "info",
            message: "已取消删除"
          });
        });
    },

    //表单提交
    onSubmit(formName, type) {
      this.$refs[formName].validate(async valid => {
        if (valid) {
          if (type === "记录") {
            let { data } = await this.$http.HallFunConfig.PostGameNotice(
              this.form
            );
             if(data.code === 1 && data.msg === 'ok'){
               this.initdata({ page: this.currentPage, limit: this.limit, title: this.searchinput });
            }
          } else if (type === "编辑") {
            // console.log(this.form);

            let resdata = this.formateNum(this.form);
            // console.log(resdata);
            if (resdata.type_id === 2) {
              resdata.image_url = "image_url";
            }
            // console.log(resdata);
            let { data } = await this.$http.HallFunConfig.PutGameNotice(
              resdata
            );
              if(data.code === 1 && data.msg === 'ok'){
               this.initdata({ page: this.currentPage, limit: this.limit, title: this.searchinput });
            }
          }
          // this.visiblity = false;
           this.editForm("添加", false, {});
        } else {
          console.log("error submit!!");
          return false;
        }
      });
    },

    editForm(title, visible, form) {
      this.title = title;
      this.visible = visible;
      this.form = form;
    },

    formateData(res) {
      res.forEach(item => {
        item.type_id = item.type_id === 1 ? "文字" : "图片";
        item.show_status = item.show_status === 1 ? "展示" : "隐藏";
        if (item.tag === 1) {
          item.tag = "没有标签";
        } else if (item.tag === 2) {
          item.tag = "新";
        } else {
          item.tag = "热门";
        }
      });
      return res;
    },

    formateNum(item) {
      if (
        typeof item.type_id === "number" &&
        typeof item.tag === "number" &&
        typeof item.show_status === "number"
      )
        return;

      item.type_id = item.type_id === "文字" ? 1 : 2;
      item.show_status = item.show_status === "展示" ? 1 : 2;
      if (item.tag === "没有标签") {
        item.tag = 1;
      } else if (item.tag === "新") {
        item.tag = 2;
      } else {
        item.tag = 3;
      }

      return item;
    },
    async initdata(params) {
      let { data } = await this.$http.HallFunConfig.GetGameNotice(params);
      let fres = this.formateData(data.data);
      this.tableData = fres;
      this.total = data.total;
      console.log(data);
    }
  }
};



